import request from '@/static/request/request.js'
export default {
	namespaced: true, //
	state: {
		// 
		academyList:[]
	},
	mutations: {
		// 
		setAcademyData(state, data) {
			state.academyList = data
		}
	},
	actions: {
		
		// 
		getAcademy(ctx) {
			request.http.post('/api/academy/cascade').then(res => {
				ctx.commit('setAcademyData', res.data)
			})
		}
	}
}
