import Crypto from './crypto.js';
import Config from './config.js'
import './hmac.js';
import './sha1.js';
import { Base64 } from './base64.js';

const uploadFileSize = 1024 * 1024 * 1; // 100m

export default {
	_getPolicy() {
		let policyText = {
			"expiration": "2050-01-01T12:00:00.000Z", //Policypolicy
			"conditions": [
				["content-length-range", 0, uploadFileSize] // 
			]
		};

		return Base64.encode(JSON.stringify(policyText))
	},
	_getSignature(message) {
		let bytes = Crypto.HMAC(Crypto.SHA1, message, Config.AccessKeySecret, {
			asBytes: true
		});
		return Crypto.util.bytesToBase64(bytes);
	},
	_getSuffix(filename) {
		let pos = filename.lastIndexOf('.')
		let suffix = ''
		if (pos != -1) {
			suffix = filename.substring(pos)
		}
		return suffix;
	},
	/* 
	    oss  
		
		goods/images/2021-02-25/ 
	 */
	getFileName(name,filename) {
		let nowTime = function getNowFormatDate() {
			var date = new Date();
			var seperator1 = "-";
			var year = date.getFullYear();
			var month = date.getMonth() + 1;
			var strDate = date.getDate();
			if (month >= 1 && month <= 9) {
				month = "0" + month;
			}
			if (strDate >= 0 && strDate <= 9) {
				strDate = "0" + strDate;
			}
			var currentdate = year + seperator1 + month + seperator1 + strDate;
			return currentdate;
		}()
		return name + '/' +  new Date().getTime() + Math.random().toString(36).substring(3, 20) + this._getSuffix(filename)
	},
	/* 
	    oss  
		
		goods/video/2021-02-25/ 
	 */
	getVideoName(name, filename) {
		let nowTime = function getNowFormatDate() {
			var date = new Date();
			var seperator1 = "-";
			var year = date.getFullYear();
			var month = date.getMonth() + 1;
			var strDate = date.getDate();
			if (month >= 1 && month <= 9) {
				month = "0" + month;
			}
			if (strDate >= 0 && strDate <= 9) {
				strDate = "0" + strDate;
			}
			var currentdate = year + seperator1 + month + seperator1 + strDate;
			return currentdate;
		}()
		return name + 'faxingVideo/' + nowTime + '/' + new Date().getTime() + Math.random().toString(36).substring(3, 20) + this._getSuffix(filename)
	},
	/*  */
	getImage() {
		return new Promise((resolve, reject) => {
			uni.chooseImage({
				count: 1, //9
				sizeType: ['original', 'compressed'], //
				sourceType: ['album', 'camera'],
				success: (res) => {
					resolve(res)
				}
			})
		});
	},
	/*  */
	getVideo() {
		return new Promise((resolve, reject) => {
			uni.chooseVideo({
				count: 1,
				sourceType: ['camera', 'album'],
				success: function(res) {
					if (res.size > uploadFileSize) {
						uni.showToast({
							title: '' + uploadFileSize,
							icon: 'none',
							duration: 1000
						});
						return;
					}
					resolve(res.tempFilePath)
				},
				fail: () => {
					uni.showToast({
						title: '',
						icon: 'none',
						duration: 2000
					});
				}
			})
		});
	},
	// STS
	get_STS() {
		// 
		/* return new Promise((resolve, reject) => {
		    let name = new Date().getTime() + Math.random();
		    let operate = 'uploadImg';
		    let that = this;
		    uni.request({
		        method: "GET",
		        url: "*************", // 
		        data: {
		            name,
		            operate,
		            sign: Crypto.MD5(name + operate + "bayinabayin888").toString()
		        },
		        success(res) {
		            if (res.data.code != 200) {
		                that.$alert(res.data.message || res.data.msg)
		            } else {
		                resolve(res.data.data)
		            }
		        },
		        error(err) {
		            reject(err)
		        }
		    })
		}) */
		// 
		return new Promise((resolve, reject) => {
			let policy = this._getPolicy();
			let res = {
				accessId: Config.OSSAccessKeyId,
				host: Config.uploadImageUrl,
				policy,
				signature: this._getSignature(policy),
			};
			resolve(res)
		})
	},
};
